<template>
	<div class="add-product-wrapper">
		<div class="header">
			<div class="title">新增产品</div>
			<div>
				<button class="square-btn " @click="goProduct">取消</button>
				<button class="square-btn primary split-area" @click="addProduct">
					确定
				</button>
			</div>
		</div>
		<div class="split-line"></div>
		<div class="main-content">
			<el-form
				:model="ruleForm"
				:rules="rules"
				ref="ruleForm"
				label-width="200px"
				class="demo-ruleForm"
			>
				<div class="base-info">
					<div class="title">基本信息</div>

					<div class="info-content">
						<div class="info-left">
							<el-form-item label="产品名称：" prop="product_name">
								<el-input v-model="ruleForm.product_name"></el-input>
							</el-form-item>
							<el-form-item label="产品英文名称" prop="product_english_name">
								<el-input v-model="ruleForm.product_english_name"></el-input>
							</el-form-item>
							<el-form-item label="产品关键字：" prop="product_keywords">
								<el-input v-model="ruleForm.product_keywords"></el-input>
							</el-form-item>
							<el-form-item label="产品原价：" prop="original_price">
								<el-input v-model="ruleForm.original_price">
									<template slot="append">元</template>
								</el-input>
							</el-form-item>
							<el-form-item label="产品现价：" prop="actual_price">
								<el-input v-model="ruleForm.actual_price">
									<template slot="append">元</template>
								</el-input>
							</el-form-item>
							<el-form-item label="产品简介" prop="product_introduction">
								<el-input
									v-model="ruleForm.product_introduction"
									type="textarea"
									:autosize="{ minRows: 3, maxRows: 3 }"
									resize="none"
								></el-input>
							</el-form-item>
						</div>
						<div class="info-right">
							<el-form-item label="上传产品LOGO：" prop="product_logo">
								<el-upload
									class="package-uploader"
									:action="baseUrl + '/image/upload'"
									:multiple="false"
									name="file"
									:on-success="uploadLogoSuccess"
									:show-file-list="false"
									accept=".jpg, .jpeg, .png"
									:before-upload="beforeImgUpload"
									:headers="{ jwt: token }"
								>
									<div v-if="!ruleForm.product_logo" class="upload-img-single">
										<img
											src="@/assets/images/upload_img.png"
											style="margin-top: 32px;"
										/>
										<div>只允许上传一个图片</div>
									</div>
									<div v-else class="upload-img-single">
										<img
											style="width: 100%;height:100%;border-radius: 4px;"
											:src="ruleForm.product_logo"
										/>
										<div class="hidden-area">
											点击替换图片
										</div>
									</div>
								</el-upload>
							</el-form-item>
						</div>
					</div>
				</div>
				<div class="other-info">
					<div class="title">其他信息</div>

					<div class="info-content">
						<div class="info-left">
							<el-form-item label="生成私有key：" prop="private_key">
								<div v-if="!ruleForm.private_key">
									<el-input
										v-model="ruleForm.private_key"
										:disabled="true"
										placeholder="点击右边生成私有key"
									></el-input>
									<div
										class="square-btn primary"
										style="margin-left: 15px;"
										@click="requestKey"
									>
										生成Key
									</div>
								</div>
								<div v-else style="height: 47px;">
									{{ ruleForm.private_key }}
								</div>
							</el-form-item>
							<el-form-item
								v-for="(product_package, index) in ruleForm.product_packages"
								:label="'安装包环境' + (index + 1) + '：'"
								:key="product_package.index"
								:prop="'product_packages.' + index + '.envir'"
								:rules="{
									required: true,
									message: '安装包环境不能为空',
									trigger: 'blur',
									validator: checkSpace,
								}"
							>
								<el-input v-model="product_package.envir"></el-input>
								<el-upload
									class="package-uploader"
									:action="baseUrl + '/product/upload'"
									:multiple="false"
									name="file"
									:on-success="(res) => uploadPackageSuccess(res, index)"
									:on-progress="(res,file)=>uploadPackageProgress(res,file,index)"
									:on-error="(err) => uploadPackageFail(err, index)"
									:data="{
										systemOs: product_package.envir,
									}"
									:headers="{ jwt: token }"
									:show-file-list="false"
									:disabled="product_package.isUpload && !product_package.fail"
								>
									<div
										v-if="!product_package.package_name && !product_package.isUpload"
										class="square-btn primary"
										style="margin-left: 15px;"
									>
										上传安装包
									</div>
									<div
										v-if="product_package.package_name && !product_package.isUpload"
										style="margin-left: 15px;"
									>
										{{ product_package.package_name }}
									</div>
								<el-progress :color="!product_package.fail?'rgb(64, 158, 255)':'rgb(245, 108, 108)'" v-if="product_package.isUpload" :percentage="product_package.percent"></el-progress>

								</el-upload>
								<div
									v-if="index != 0 && !product_package.isUpload"
									class="square-btn danger"
									style="margin-left: 10px;"
									@click="deletePackage(index)"
								>
									删除安装包
								</div>
							</el-form-item>
							<div
								class="square-btn primary"
								style="margin-left: 200px;"
								@click="addPackage"
							>
								新增安装包
							</div>
						</div>
						<div class="info-right">
							<el-form-item label="上传产品图：" prop="product_images">
								<el-upload
									class="package-uploader"
									:action="baseUrl + '/image/upload'"
									name="file"
									:on-success="uploadProductImg"
									:show-file-list="false"
									:multiple="true"
									v-if="!ruleForm.product_images.length"
									accept=".jpg, .jpeg, .png"
									:before-upload="beforeImgUpload"
									:headers="{ jwt: token }"
								>
									<div class="upload-img-single">
										<img
											src="@/assets/images/upload_img.png"
											style="margin-top: 32px;"
										/>
										<div>允许一次上传多张图片</div>
									</div>
								</el-upload>
								<div v-else class="product_images">
									<div
										v-for="(item, index) in ruleForm.product_images"
										:key="index"
										class="product_image"
									>
										<el-upload
											class="product_image"
											:action="baseUrl + '/image/upload'"
											name="file"
											:on-success="changeImg"
											:show-file-list="false"
											:multiple="false"
											accept=".jpg, .jpeg, .png"
											:before-upload="beforeImgUpload"
											:headers="{ jwt: token }"
										>
											<img
												style="width: 100%;height: 100%;border-radius: 4px;"
												:src="item.imageSrc"
											/>
											<div class="hidden-area" @click="changeIndex(index)">
												点击替换图片
											</div>
										</el-upload>
										<i
											class="delete-icon el-icon-circle-close"
											@click="deleteImg(index)"
										></i>
									</div>
									<el-upload
										class="product_image"
										:action="baseUrl + '/image/upload'"
										name="file"
										:on-success="uploadProductImg"
										:show-file-list="false"
										:multiple="true"
										accept=".jpg, .jpeg, .png"
										:before-upload="beforeImgUpload"
										:headers="{ jwt: token }"
									>
										<div>
											<img
												src="@/assets/images/upload_img.png"
												style="margin-top: 15px;"
											/>
											<div class="upload-text">允许一次上传多张图片</div>
										</div>
									</el-upload>
								</div>
							</el-form-item>
						</div>
					</div>
				</div>
			</el-form>
		</div>
	</div>
</template>

<script>
import { createKey, addProductSeries } from "../../api/product";
import { baseUrl } from "../../../config";
import { validFileType } from "@/utils/index";
export default {
	data() {
		return {
			// 当前包的索引
			// current_package: "",
			// 当前产品图的索引
			current_product_img_index: "",
			ruleForm: {
				product_name: "",
				product_english_name: "",
				product_keywords: "",
				original_price: "",
				actual_price: "",
				product_introduction: "",
				product_logo: "",
				private_key: "",
				imageId: "",
				logo: "",
				product_packages: [
					{
						envir: "",
						package_name: "",
						package_path: "",
						id: "",
						status: 1,
						downloadTimes: 0,
						url: "",
						isUpload: false,
						percent: 0,
						fail: false
					},
				],
				product_images: [],
			},
			baseUrl: "",
			rules: {
				product_name: [
					{
						required: true,
						message: "请输入产品名称",
						validator: this.checkSpace,
						trigger: "blur",
					},
				],
				product_english_name: [
					{
						required: true,
						message: "请输入产品英文名称",
						validator: this.checkSpace,
						trigger: "blur",
					},
					{
						message: "产品英文名称只能输入英文",
						pattern: /^[a-zA-Z\s]+$/,
						trigger: "blur"
					}
				],
				product_keywords: [
					{
						required: true,
						message: "请输入产品关键字",
						validator: this.checkSpace,
						trigger: "blur",
					},
				],
				original_price: [
					{ required: true, message: "请输入产品原价", validator: this.checkSpace, trigger: "blur" },
					{
						message: "产品价格只能为数字",
						pattern: /(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/,
						trigger: "blur",
					},
				],
				actual_price: [
					{ required: true, message: "请输入产品现格", validator: this.checkSpace, trigger: "blur" },
					{
						message: "产品价格只能为数字",
						pattern: /(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/,
						trigger: "blur",
					},
				],
				product_introduction: [
					{
						required: true,
						message: "请输入产品简介",
						validator: this.checkSpace,
						trigger: "blur",
					},
				],
				product_logo: [
					{ required: true, message: "请上传产品logo", trigger: "change" },
				],
				private_key: [
					{ required: true, message: "请生成私有Key", trigger: "blur" },
				],
				product_images: [
					{ required: true, message: "请上传产品图", trigger: "blur" },
				],
			},
		};
	},
	created() {},
	mounted() {
		this.baseUrl = baseUrl;
	},
	computed: {
		token() {
			return sessionStorage.getItem("token");
		},
	},
	methods: {
		checkSpace(rule, value, callback) {
			if (!value.trim()) {
				callback(new Error("不能为空"));
			} else {
				callback();
			}
		},
		goProduct() {
			this.$router.push("/productManage");
		},
		requestKey() {
			createKey().then((res) => {
				this.ruleForm.private_key = res.data.data;
			});
		},
		deleteImg(index) {
			this.ruleForm.product_images.splice(index, 1);
		},
		addPackage() {
			this.ruleForm.product_packages.push({
				envir: "",
				package_name: "",
				package_path: "",
				id: "",
				status: 1,
				downloadTimes: 0,
				url: "",
				isUpload: false,
				percent: 0,
				fail: false
			});
		},
		deletePackage(index) {
			this.ruleForm.product_packages.splice(index, 1);
		},
		beforeImgUpload(file) {
			let isImg = validFileType(file);
			if (!isImg) {
				this.$message({
					type: "warning",
					message: "必须上传图片格式文件！",
				});
			}
			return isImg;
		},
		uploadPackageSuccess(response, index) {
			this.ruleForm.product_packages[index].fail = false;
			this.ruleForm.product_packages[index].package_name =
				response.data.productName;
			this.ruleForm.product_packages[index].id =
				response.data.id;
			this.ruleForm.product_packages[index].url =
				response.data.url;
			this.ruleForm.product_packages[index].package_path =
				this.baseUrl + "/file/" + response.data.url.replace(/.*\//g, "");
			this.ruleForm.product_packages[index].isUpload = false;
		},
		uploadPackageProgress(event, file, index) {
			this.ruleForm.product_packages[index].fail = false;
			this.ruleForm.product_packages[index].isUpload = true;
			this.ruleForm.product_packages[index].percent = Math.floor(event.percent);
		},
		uploadPackageFail(err, index) {
			this.$message({
				type: "warning",
				message: "上传安装包失败，请重新上传！"
			})
			this.ruleForm.product_packages[index].fail = true;

		},
		uploadLogoSuccess(response) {
			this.ruleForm.logo = response.data[0].imageUrl;
			this.ruleForm.product_logo =
				this.baseUrl +
				"/file/" +
				response.data[0].imageUrl.replace(/.*\//g, "");
			this.ruleForm.imageId = response.data[0].id;
			this.$refs.ruleForm.validateField("product_logo");
		},
		uploadProductImg(response) {
			this.ruleForm.product_images.push({
				imageId: response.data[0].id,
				imageSrc:
					this.baseUrl +
					"/file/" +
					response.data[0].imageUrl.replace(/.*\//g, ""),
				imageUrl: response.data[0].imageUrl,
			});
			this.$refs.ruleForm.validateField("product_images");
		},
		changeImg(response) {
			this.$set(this.ruleForm.product_images, this.current_product_img_index, {
				imageId: response.data[0].id,
				imageSrc:
					this.baseUrl +
					"/file/" +
					response.data[0].imageUrl.replace(/.*\//g, ""),
			});
		},
		changeIndex(index) {
			this.current_product_img_index = index;
		},
		addProduct() {
			for (let i = 0; i < this.ruleForm.product_packages.length; i++) {
				if (!this.ruleForm.product_packages[i].package_name) {
					this.$message({
						type: "warning",
						message: "安装包不能为空",
					});
					return;
				}
			}
			this.$refs.ruleForm.validate((bool, obj) => {
				console.log(bool, obj);
				if (bool) {
					let data = {};
					data.images = this.ruleForm.product_images.map((item) => {
						return {
							imageId: item.imageId,
							imageUrl: item.imageUrl,
						};
					});
					let productSeries = {};
					let tempData = this.ruleForm;
					productSeries.chName = tempData.product_name;
					productSeries.enName = tempData.product_english_name;
					productSeries.imageId = tempData.imageId;
					productSeries.introduction = tempData.product_introduction;
					productSeries.keyWord = tempData.product_keywords;
					productSeries.logo = tempData.logo;
					productSeries.originalPrice = tempData.original_price;
					productSeries.actualPrice = tempData.actual_price;
					productSeries.privateKey = tempData.private_key;
					(productSeries.status = 0), (productSeries.totalDownloads = 0);
					productSeries.activateTimes = 0;
					data.productSeries = productSeries;
					data.products = this.ruleForm.product_packages.map((item) => {
						return {
							downloadTimes: 0,
							status: 1,
							id: item.id,
							systemOs: item.envir,
							productName: item.package_name,
							url: item.url,
						};
					});
					addProductSeries(data).then((res) => {
						this.$message({
							type: "success",
							message: "新增产品成功",
						});
						this.ruleForm = {
							product_name: "",
							product_english_name: "",
							product_keywords: "",
							original_price: "",
							actual_price: "",
							product_introduction: "",
							product_logo: "",
							private_key: "",
							imageId: "",
							logo: "",
							product_packages: [
								{
									envir: "",
									package_name: "",
									package_path: "",
									id: "",
									status: 1,
									downloadTimes: 0,
									url: "",
									isUpload: false,
									percent: 0
								},
							],
							product_images: [],
						};
					});
				}
			});
		},
	},
};
</script>

<style lang="scss" scoped>
.el-progress {
	display: inline-block;
	width: 200px;
	margin-left: 10px;
}
.add-product-wrapper {
	.square-btn {
		width: 120px;
		height: 44px;
		line-height: 42px;
		font-size: 18px;
		color: #313552;
		border-radius: 4px;
		background-color: rgba(66, 90, 203, 0.1);
		border: 1px solid rgba(49, 53, 82, 0.4);
		font-size: 18px;
		font-family: "alifont";
		text-align: center;
		display: inline-block;
		cursor: pointer;
	}
	.split-area {
		margin-left: 32px;
	}
	.primary {
		background-color: #425acb;
		color: #fff;
	}
	.danger {
		background-color: #f56c6c;
		color: #fff;
		border: 1px solid #f56c6c;
	}
	.header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		.title {
			font-size: 24px;
			color: #313552;
			font-weight: bold;
		}
	}
	.split-line {
		height: 1px;
		background-color: #d9d9d9;
		margin: 36px 0 32px 0;
	}
	.main-content {
		.base-info,
		.other-info {
			.product_images {
				display: flex;
				flex-wrap: wrap;
				justify-content: flex-start;
				.product_image {
					width: 100px;
					height: 100px;
					margin-bottom: 30px;
					position: relative;
					border-radius: 4px;
					text-align: center;
					font-size: 12px;
					color: #595959;
					border-radius: 5px;
					border: 1px solid #d5d9ed;
					text-align: center;
					background-color: #eceffc;
					position: relative;
					/deep/.el-upload {
						width: 100%;
						height: 100%;
					}
					.upload-text {
						line-height: 12px;
					}
					img {
						width: 32px;
						height: 32px;
					}
					.hidden-area {
						position: absolute;
						left: 0;
						top: 0;
						display: none;
						width: 100%;
						height: 100%;
						background-color: rgba(0, 0, 0, 0.2);
						color: #f9fafa;
						// display: flex;
						align-items: center;
						justify-content: center;
						cursor: pointer;
					}
				}
				.product_image {
					position: relative;
					.delete-icon {
						z-index: 1000;
						position: absolute;
						right: 0;
						top: 0;
						color: red;
						font-size: 20px;
						transform: translate(50%, -50%);
						cursor: pointer;
					}
				}
				.product_image:nth-child(2n) {
					margin-left: 30px;
				}
				.product_image:hover .hidden-area {
					display: flex;
				}
			}
			.title {
				font-size: 20px;
				border-left: 8px solid #425acb;
				padding-left: 7px;
			}
			.info-content {
				display: flex;
				align-items: flex-start;
				.el-input,
				.el-textarea {
					width: 380px;
					/deep/.el-input__inner {
						height: 100%;
					}
				}
				.el-input {
					height: 46px;
				}
				.info-left {
					flex: 2;
					.package-uploader {
						display: inline-block;
					}
					/deep/.el-input-group__append {
						height: 100%;
					}
				}
				.info-right {
					flex: 1;
					.upload-img-single {
						width: 160px;
						height: 160px;
						font-size: 14px;
						color: #595959;
						border-radius: 5px;
						border: 1px solid #d5d9ed;
						text-align: center;
						background-color: #eceffc;
						position: relative;
						.hidden-area {
							position: absolute;
							left: 0;
							top: 0;
							display: none;
							width: 100%;
							height: 100%;
							background-color: rgba(0, 0, 0, 0.2);
							color: #f9fafa;
							// display: flex;
							align-items: center;
							justify-content: center;
							cursor: pointer;
						}
					}
					.upload-img-single:hover .hidden-area {
						display: flex;
					}
				}
			}
		}
	}
}
</style>
