// 格式化时间
export function formatTime(date, length = 16) {
	date = new Date(+new Date(date) + 8 * 3600 * 1000);
	return date
		.toJSON()
		.substr(0, length)
		.replace("T", " ");
}

// 图片类型验证
  export function validFileType(file) {
	const fileTypes = [
		"image/apng",
		"image/bmp",
		"image/gif",
		"image/jpeg",
		"image/pjpeg",
		"image/png",
		"image/svg+xml",
		"image/tiff",
		"image/webp",
		"image/x-icon"
	  ];
	return fileTypes.includes(file.type);
  }